.main-testimonials {
  background-color: #f8fcfd;

  &_list {
    display: flex;
    align-items: center;
    align-content: center;
  }

  &_item {
    flex-basis: 33%;
    margin-right: 20px;
    background-color: #fff;
    border-radius: 5px;
    box-shadow: 0 1px 2px rgba(48, 54, 60, .1);
    transition: all .3s ease;

    &:last-child { margin-right: 0; }

    &:hover {
      box-shadow: 0 17px 22px 5px rgba(48, 54, 60, .1);
    }
  }

  &_box {
    padding: 20px;
  }

  &_info {
    display: flex;
    align-items: center;
    align-content: center;
    margin-bottom: 20px;
  }

  &_name {
    color: #30363c;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 500;
  }

  &_photo {
    margin-right: 20px;

    &-box {
      width: 60px;
      height: 60px;
      background-color: #2f383e;
      border-radius: 50%;
      overflow: hidden;
    }
  }

  &_content {
    color: #30363c;
    font-family: 'Roboto';
    font-size: 14px;
    font-weight: 300;
    line-height: 25px;
  }
}
